.Memory {
  height: clamp(250px, 35vh, 350px);
  /* min-width: 350px; */
  /* max-width: 24vw; */
  /* width: clamp(320px, calc(25% - 40px), 500px); */
  /* display: flex; */
  /* flex-direction: column; */
  /* gap: 8px; */
  margin: 8px;
  /* align-items: center; */
  flex-basis: 350px;
  min-width: 350px;
  flex-grow: 1;
  flex-shrink: 1;
  /* justify-content: flex-start; */
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

.Memory .memoryAvatar {
  border-radius: 16px 16px 0 0;
  width: 100%;
  flex-grow: 1;
  min-height: 100%;
  max-height: 100%;
  height: 100%;
  /* max-height: 55%; */
  /* min-height: 45%; */
  /* height: calc(100% - 3rem - 8px); */
  object-fit: cover;
}

.Memory:hover {
  background-color: var(--accent-color);
}

.Memory .memoryName {
  font-size: 1.5em;
  /* padding-inline: 8px; */
  /* text-align: center; */
  text-align: center;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  width: 100%;
  /* flex: 1; */
  /* flex-basis: 4em; */
  /* flex-shrink: 4em; */
  /* flex-grow: 4em; */
  /* height: 2em; */
  /* overflow: hidden; */
  /* Remove text-overflow, white-space, and text-align properties */
}

.Memory .memoryDescription{
  /* flex: 1; */
  padding-inline: 8px;
  text-align: center;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.memoryBody{
  /* width: 100%; */
  position: absolute;
  background-color: #FFFFFF33;
  /* align-self: flex-end; */
  inset: auto 0 0 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
  /* height: 100%; */
  backdrop-filter: blur(10px);
  padding: 8px;
}