.Account{
    height: clamp(100px, 25vh, 115px);
    width: clamp(100px, 25vh, 115px);
    flex-shrink: 0;
    /* padding: 8px; */
    /* display: flex; */
    /* flex-direction: column; */
    /* gap: 8px; */
    /* align-items: center; */
    /* justify-content: center; */
    overflow: hidden;
    border-radius: 1.5em;
    /* background-color: var(--accent-color); */
    cursor: pointer;
}

.Account:hover{
    background-color: var(--accent-color);
}

.accountAvatar{
    width: 100%;
    object-fit: cover;
    height: 100%;
    /* border-radius: 50%;  */
}

.accountName{
    width: 100%;
    position: absolute;
    inset: auto 0 0 0;
    z-index: 1;
    background-color: #FFFFFF33;
    backdrop-filter: blur(10px);
    padding: 8px;
    color: var(--primary-text-color);
    text-align: center;
    /* display: -webkit-box; */
    /* -webkit-line-clamp: 1; */
    /* -webkit-box-orient: vertical; */
    /* overflow: hidden; */
}

.Account:hover .accountName{
    color: var(--contrast-text-color);
    background-color: #03ffff69;
}

.Account#user .accountName{
    background-color: #03ffff75;
    color: var(--contrast-text-color);
}